@import '../../../theme/variables.scss';

.root {
  :global(.board) {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    .help {
      color: $text-color2;
      font-size: 16px;
      white-space: pre-line;
      line-height: 0.8em;
      margin-top: 20px;
      text-align: center;
    }
  }
  
  .player {
    width: 480px;
    height: 360px;
    margin-top: 25px;
    margin-bottom: 15px;
    background-color: #000;
  
    video {
      width: 100%;
      height: 100%;    
    }
  }
  
  .progress {
    width: 360px;
    margin-bottom: 10px;
  }
  
  .buttons {
    text-align: center;
    button {
      width: 150px;
      margin: 6px;
      i {
        margin-right: 10px;
      }
    }
  }
}



